import '../../index.css'
import Typed from 'react-typed';
import Refor from '../../assets/bg1.mp4';
import './PentasG';
import logo from '../../assets/logo.png'
import MainG from './Main';
import CountdownG from './Countdown';
import AboutG from './About';
import DateG from './Date';
import PentasG from './PentasG'
import {useState} from 'react';

export default function Main() {
  return (
    <div className='relative mx-auto justify-center text-center flex flex-col w-full h-screen bg-fixed'>
      
      <div>
        <video className='absolute top-0 left-0 w-full h-full object-cover' src={Refor} autoPlay loop muted></video>  
      </div>
      

      <div className='absolute flex flex-col justify-center items-center top-0 w-full h-full'>
        
        <img src={logo} className=" h-80 w-80" alt="Logo refor" />
        <p className='text-white m-2 text-base'>Kami mengundang dan akan menunggu Anda di :</p>
        <Typed className='text-white text-center text-base font-bold'
          strings={['Reformatika 2021' ]}
          startDelay={2500}
          typeSpeed={25}
          cursorChar='|'
          />
          <a href="#next" class="flex items-center justify-center text-white rounded-2xl bg-red-700 mt-10 w-28 h-12">Join Now!</a>
         </div>
    </div>
  )
}